.footer-inner {
  width: 1440px;
  opacity: 1;
  margin: 0 auto;
}

.footer-top-col {
  border-top: 2px solid #eee;
  margin: 40px 0;
  padding-top: 40px;
}

.footer-col4 ul {
  padding-left: 0px;
}

.footer-col a {
  color: #172026 !important;
  text-decoration: none;
}

.footer-col .footer-element {
  margin-bottom: 15px;
}

.footer-col li {
  color: #545D63;
}

.footer-social-icons {
  margin-top: 15px;
}

.footer-social-icons span {
  margin-right: 20px;
}

.footer-bottom-col {
  padding: 0px 40px;
  border-top: 2px solid #eee;
}

.footer-copyright {
  text-align: center;
  margin-top: 20px;
}

.footer-policy li {
  float: left;
  margin-right: 20px;
  margin-top: 5px;
}

.footer-policy li a {
  color: #545D63;
}

@media (min-width: 320px) and (max-width: 768px) {
  .footer-bottom-col {
    padding: 0px;
  }
  .footer-items-details {
    display: -webkit-box !important;
    display: -ms-flexbox !important;
    display: flex !important;
    -webkit-box-orient: vertical !important;
    -webkit-box-direction: reverse !important;
        -ms-flex-direction: column-reverse !important;
            flex-direction: column-reverse !important;
  }
  .footer-copyright {
    margin-bottom: 25px;
  }
  .footer-col {
    width: 100% !important;
  }
  .footer-col ul {
    padding: 0px;
  }
}
